/* Reusable Color Variables */
:root {
    --background-color: #3aafa9;
    --header-color: #17242a;
    --container-color: #2b7a77;
    --primary-color: #def2f1;
    --secondary-color: #eedac1;
    --alt-color: #13f5f5;
}

.timeline {
    position: relative;
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    margin-top: 100px;
    padding: 15px 0;
}

.timeline::after {
    content: '';
    position: absolute;
    width: 2px;
    background: var(--container-color);
    top: 0;
    bottom: 0;
    left: 50%;
    margin-left: -1px;
}

.container {
    padding: 15px 30px;
    position: relative;
    background: inherit;
    width: 50%;
}

.container.left {
    left: -5%;
}

.container.right {
    left: 50%;
}

.container::after {
    content: '';
    position: absolute;
    width: 16px;
    height: 16px;
    top: calc(50% - 8px);
    right: -8px;
    background: var(--primary-color);
    border: 2px solid var(--container-color);
    border-radius: 16px;
    z-index: 1;
}

.container.right::after {
    left: -8px;
}

.container::before {
    content: '';
    position: absolute;
    width: 50px;
    height: 2px;
    top: calc(50% - 1px);
    right: 8px;
    background: var(--container-color);
    z-index: 1;
}

.container.right::before {
    left: 8px;
}

.container .date {
    position: absolute;
    display: inline-block;
    top: calc(50% - 10px);
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    color: var(--container-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    z-index: 1;
}

.container .date {
    position: absolute;
}

.container.left .date {
    right: var(--date-offset, -140px);
}

.container.right .date {
    left: var(--date-offset, -140px);
}

.container.education .date {
    --date-offset: -60px;
}

.container .icon {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    top: calc(50% - 20px);
    background: var(--primary-color);
    border: 2px solid var(--secondary-color);
    border-radius: 40px;
    font-size: 18px;
    color: var(--container-color);
    z-index: 1;
}

.container.left .icon {
    right: 56px;
}

.container.right .icon {
    left: 56px;
}

.container .content {
    padding: 30px 90px 30px 30px;
    background: var(--container-color);
    position: relative;
    border-radius: 0 500px 500px 0;
}

.container.right .content {
    padding: 50px 30px 30px 130px;
    border-radius: 500px 0 0 500px;
}

.container .content h2,
h5 {
    margin: 0 0 10px 0;
    font-size: 18px;
    font-style: italic;
    padding-bottom: 5px;
}


.container .content p {
    padding-bottom: 15px;
    margin: 0;
    font-size: 16px;
    line-height: 1.2rem;
}

/*  RESPONSIVENESS In Progress */
@media (max-width: 767.98px) {
    .timeline::after {
        left: 90px;
    }

    .container {
        width: 100%;
        padding-left: 120px;
        padding-right: 30px;
    }

    .container.right {
        left: 0%;
    }

    .container.left::after,
    .container.right::after {
        left: 82px;
    }

    .container.left::before,
    .container.right::before {
        left: 100px;
        border-color: transparent var(--primary-color) transparent transparent;
    }

    .container.left .date,
    .container.right .date {
        right: auto;
        left: 15px;
    }

    .container.left .icon,
    .container.right .icon {
        right: auto;
        left: 146px;
    }

    .container.left .content,
    .container.right .content {
        padding: 30px 30px 30px 90px;
        border-radius: 500px 0 0 500px;
    }
}
